$tabletwiev: 768px;
body {
  background: #050505;
}
.header {
  height: 100vh;
  #myVideo {
    margin: 0 auto;
    position: absolute;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
  }
  .wrapper {
    position: absolute;
    top: 0;
    right: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    background: rgba(0, 0, 0, 0.6);
    width: 100%;
    padding: 0 15%;
    height: 100%;
    transform: translate(50%);
    .icons {
      flex: 1;
      display: flex;
      justify-content: space-around;
      flex-direction: row;
      align-items: center;
      width: 100%;
      a {
        width: 49px;
        height: 49px;
        transition: transform 0.5s linear;
        transform: scale(1);
        img {
          width: 100%;
          height: auto;
        }
      }
      a:hover {
        transform: scale(1.2);
      }
    }
    .logo {
      flex: 1;
      display: flex;
      align-items: flex-end;
      img {
        width: 236px;
        height: 71px;
      }
    }
    .arrow {
      position: absolute;
      bottom: 31px;
      img {
        transition: transform 1s;
        transform: scale(1.5);
        width: 31px;
        height: 19px;
        animation-play-state: running;
        animation: scale 1s infinite linear both;
      }
      img:hover {
        animation-play-state: paused;
      }
      @keyframes scale {
        0% {
          transform: scale(1.5);
        }
        50% {
          transform: scale(2);
        }
        100% {
          transform: scale(1.5);
        }
      }
    }
  }
}
.main-content {
  width: 100%;
  background: #050505;
  .navigation-panel {
    display: flex;
    padding: 56px 0;
    flex-direction: column;
    a {
      text-transform: uppercase;
      font-family: "Roboto", sans-serif;
      font-size: 30px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.6;
      letter-spacing: 1.5px;
      text-align: left;
      //   color: white;
      color: rgb(32, 32, 32);
    }
    // a:hover {
    //   color: white;
    // }
  }
}

.block-content {
  max-width: $tabletwiev;
  margin: 0 auto;
  margin-bottom: 75px;
  .block-with-employee {
    width: 100%;
    flex-direction: column;
    padding: 1px 0;
    .parent {
      margin: 6px 0;
      width: 100%;
      height: 100%;

      // height: 300px;
      overflow: hidden;
      .child {
        min-height: 300px;
        position: relative;
        width: 100%;
        height: 100%;
        // background-color: black; /* fallback color */
        background-image: url("../img/image.jpg");
        background-position: center;
        // background-attachment: fixed;
        background-repeat: no-repeat;
        // background-size: cover;
        background-size: 200%;
        transition: all 0.5s;
        .text {
          // overflow-y: scroll;
          padding: 15%;
          transition: all 0.5s;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100%;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          h3 {
            margin: 5px 0;
            text-transform: uppercase;
            font-family: "Roboto", sans-serif;
            font-size: 30px;
            font-weight: 400;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.6;
            letter-spacing: 1.5px;
            text-align: center;
            color: white;
          }
          p {
            margin: 5px 0;
            font-family: "Roboto", sans-serif;
            font-size: 17px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.6;
            letter-spacing: 0.85px;
            text-align: center;
            color: white;
          }
        }
      }
      .child::before {
        content: "";
        display: block;
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.6);
        // background-color: rgba(52, 73, 94, 0.75);
      }
    }
    .block-faq {
      margin: 10px 0;
      .collapsible {
        // background-color: #777;
        background-color: #252525;
        color: white;
        cursor: pointer;
        padding: 18px;
        width: 100%;
        border: none;
        text-align: left;
        outline: none;
        font-family: "Roboto", sans-serif;
        font-size: 18px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.6;
        letter-spacing: 1.5px;
        // font-size: 15px;
      }
      .collapsible:after {
        content: "\002B";
        color: white;
        font-weight: bold;
        float: right;
        margin-left: 5px;
      }
      .active:after {
        content: "\2212";
      }
      .active,
      .collapsible:hover {
        // background-color: #555;
        background-color: #181818;
      }
      .content {
        padding: 0 18px;
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.2s ease-out;
        background-color: #f1f1f1;
        background: white;
        p {
          font-family: "Roboto", sans-serif;
          font-size: 15px;
          font-weight: 500;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.6;
          letter-spacing: 1.5px;
        }
      }
    }
    .parent:hover .child,
    .parent:focus .child {
      // transform: scale(1.2);
      background-size: 250%;
      .text {
        // transform: translate(-50%, -50%) scale(0.8);
      }
    }
    .container {
      // background: grey;
      // width: 90%;
      margin: 0 auto;
      padding: 10px 0;
      .block-with-rools {
        h3 {
          font-family: "Roboto", sans-serif;
          font-size: 24px;
          font-weight: 400;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.6;
          letter-spacing: 1.5px;
          color: white;
        }
        ol li {
          font-family: "Roboto", sans-serif;
          font-size: 18px;
          font-weight: 400;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.6;
          letter-spacing: 1.5px;
          color: white;
        }
      }
    }
  }
}

.foot {
  .start-game {
    // margin: 0 auto;
    display: flex;
    align-items: center;
    position: relative;
    min-height: 360px;
    background-image: url("../img/image.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .text {
      margin: 0 auto;
      z-index: 1;
      // position: absolute;
      // top: 50%;
      // left: 50%;
      // transform: translate(-50%,-50%);
      h3 {
        text-transform: uppercase;
        font-family: "Roboto", sans-serif;
        font-size: 24px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: 1.2px;
        text-align: center;
        color: #ffffff;
      }
      a {
        text-decoration: none;
        .button {
          transition: all 0.5s;
          display: flex;
          justify-content: center;
          align-items: center;
          color: black;
          width: 256px;
          height: 73px;
          margin: 0 auto;
          background-color: #ffffff;
          font-family: "Roboto", sans-serif;
          font-size: 28px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: 1.2px;
          text-align: center;
        }
        .button:focus,
        .button:hover {
          transform: scale(1.1);
        }
      }
    }
  }
  .start-game::before {
    content: "";
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
  }
}
.wrapper {
  //   width: 75%;
  width: 67.5%;
  max-width: 768px;
  margin: 0 auto;
}
.active-button {
  color: white !important;
}
.empty {
  margin: 0 !important;
  height: 0 !important;
}
.slick-prev {
  left: 2% !important;
  z-index: 1;
}
.slick-next {
  right: 2% !important;
  z-index: 1;
}
